.cuadroTablaCoche{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    padding: 10px;
}

.tablaCoche{
    width: 100%;
    height: 100%;
    text-align: initial;
    border-collapse: collapse;

}

th {
    color: rgb(33, 49, 71);
    width: 33%;
    font-size: 16px;
    padding-left: 50px;
    text-align: initial;
    text-transform: capitalize;
}

td{
    padding-left: 50px;
    width: 33%;
    border-bottom: 1px solid rgb(241, 241, 241);
    font-size: 14px;
    color: rgb(109, 109, 109);
    text-transform: capitalize;
}